body {
	margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
  background: #1a1a2e;
} 

path {
  fill: #22224a;
  stroke: #537399;
  stroke-opacity: 0.1; 
}

.map {
    margin-right: 20rem;
}

.navigation {
    height: 100vh;
    width: 100%;
    background: white;
    display: inline-block;
    vertical-align: middle;
    margin-left: 70%;
    background: #22224a;
    top: 0;
    position: fixed;

}

.tooltip {
  font-family: 'Monsterrat', sans-serif;
  background: #22224a;
  text-align: center;
  font-size: 1rem;
  height: auto;
  width: 10rem;
  color: white;
}
